import React from 'react';
import ReactDOM from 'react-dom/client';
import { BrowserRouter } from 'react-router-dom';
import "./globals.css";
import App from './page';
import Layout from './Layout';
import reportWebVitals from './reportWebVitals';

const root = ReactDOM.createRoot(document.getElementById('root'));

// Compute a sensible basename:
// - prefer CRA's PUBLIC_URL when set (used for production builds)
// - otherwise fall back to the current location path so the dev server's
//   served subpath (e.g. /spiderman) is respected
const computedBasename =
  process.env.PUBLIC_URL && process.env.PUBLIC_URL !== ""
    ? process.env.PUBLIC_URL
    : typeof window !== "undefined"
    ? // strip any trailing slash
      window.location.pathname.replace(/\/$/, "") || "/"
    : "/";

root.render(
  <React.StrictMode>
    <BrowserRouter basename={computedBasename}>
      <Layout>
        <App />
      </Layout>
    </BrowserRouter>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
